import React, {Component} from 'react';
import ContactReduxStatusForm from "../Profile/ReduxStatusForm";

class StatusForm extends Component {
    constructor(props) {

        super(props);

        this.sendStatus = this.sendStatus.bind(this)
    }

    sendStatus(formData) {
        this.props.deActivateEditMode(formData.status)
    }
    render() {

        return (
            <div>
                <ContactReduxStatusForm {...this.props} onSubmit={this.sendStatus} />
            </div>
        );
    }
}

export default StatusForm;