import React from "react";
import {Card, CardActionArea, CardContent, CardMedia, Typography} from "@material-ui/core";
import useStyles from "../../styles/postsMessagesStyles";
import avatar from '../../img/brad.jpeg'
import {ProgressLoader} from "../Loader/ProgressLoader";


const Friends = (props) => {

    const classes = useStyles()
    if (!props.profile) {
        return <ProgressLoader />
    }
    return (
        <div>
            <Card className={classes.root} >
                <CardActionArea>
                    <CardMedia
                        component="img"
                        alt={props.profile.fullName}
                        height="200"
                        image={!props.profile.photos.large ? avatar : props.profile.photos.large}
                        title={props.profile.fullName}
                    />
                    <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {props.profile.fullName}
                            </Typography>
                            <Typography gutterBottom variant="h5" component="h2">
                                About Me:
                                &nbsp;
                                {props.profile.aboutMe
                                ? props.profile.aboutMe
                                    : 'What,s up ?'
                                }
                            </Typography>
                        <Typography gutterBottom variant="h5" component="h2">
                                Locking For a Job:
                                &nbsp;
                                {props.profile.lookingForAJobDescription
                                    ? props.profile.lookingForAJobDescription
                                    : 'Don,t need a Job!'
                                }
                            </Typography>
                        <hr/>
                    </CardContent>
                </CardActionArea>
            </Card>
        </div>
    )
}

export default Friends