import React from "react";
import {Box, Container, Grid} from "@material-ui/core";
import useStyles from "../styles/dialogsStyles"
import ProfileMessages from "../components/Profile/ProfileMessages";
import ProfileForm from "../components/Profile/ProfileForm";
import Friends from "../components/Profile/Friends";
import ProfileStatus from "../components/Profile/ProfileStatus";


const Profile = (props) => {

    const classes = useStyles()

    return (

        <div className={classes.root}>
            <Container className={classes.container}>
                <h1 className={classes.title}>Profile Page</h1>
                <Grid container>
                    <Grid container spacing={3}>
                        <Grid item xs={6}>
                            <h6>Profile</h6>
                            <Friends profile={props.profile}/>
                        </Grid>
                        <Grid item xs={6}>
                            <h6>Status</h6>
                            <ProfileStatus status={props.status}
                                           updateStatusprofileThunk={props.updateStatusprofileThunk}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <div className={classes.dialogsWrapper}>
                                <h6>Messages</h6>
                                {props.dialogs.map((el, idx) => (
                                    <ProfileMessages  key={idx} message={el.message} likes={el.likes} img={el.img}/>))}
                            </div>
                            <Box mt={2}>
                                <ProfileForm
                                    addDialogs={props.addDialogs}
                                    dialogsChange={props.dialogsChange}
                                    newDialogsText={props.newDialogsText}
                                />
                            </Box>
                        </Grid>
                    </Grid>
                </Grid>
            </Container>
        </div>
    )
}

export default Profile