import React from 'react'
import {TextField} from "@material-ui/core";
import {Field, reduxForm} from "redux-form";


const ReduxStatusForm = (props) => {

    const renderTextField = ({
                                 label,
                                 input,
                                 meta: { touched, invalid, error },
                                 ...custom
                             }) => (
        <TextField
            label={label}
            placeholder={label}
            error={touched && invalid}
            helperText={touched && error}
            {...input}
            {...custom}
        />
    )

    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                <Field
                    component={renderTextField}
                    onBlur={props.deActivateEditMode}
                    value={props.status}
                    name={'status'}
                    onChange={props.chancheStatus}
                    label="Write-status"
                    autoFocus={true}
                    fullWidth={true}
                />
            </form>
        </div>
    )
}

const ContactReduxStatusForm = reduxForm(
    {
        form: 'status-form'
    }
)(ReduxStatusForm)

export default ContactReduxStatusForm