import React, {useEffect, useState} from 'react';
import {Typography} from "@material-ui/core";
import {StatusMessage} from "../Posts/StatusMessage";
import StatusForm from "../Posts/StatusForm";

const ProfileStatus = (props) => {
    const [editMode, setEditMode] = useState(false)
    const [status, setStatus] = useState(props.status)

    useEffect(() => {
        setStatus(props.status)
    }, [props.status])

   const  activateEditMode = () => {
        setEditMode(true)
    }


    const deActivateEditMode = () => {
        setEditMode(false)
        props.updateStatusprofileThunk(status)

    }

    const chancheStatus = (e) => {
        setStatus(e.currentTarget.value)
    }

    return(
        <div>
            <Typography variant={"h6"}>What are you think?</Typography>
            {editMode &&
            <StatusForm
                status={status}
                deActivateEditMode={deActivateEditMode}
                chancheStatus={chancheStatus}
            />
            }
            {!editMode &&
                <StatusMessage
                    status={status}
                    activateEditMode={activateEditMode}
                />
            }
        </div>
    )

}

export default ProfileStatus;