import React from 'react';
import useStyles from "../../styles/postsMessagesStyles";
import {Avatar, Button, List, ListItem, Typography} from "@material-ui/core";
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt'
import ThumbDownIcon from '@material-ui/icons/ThumbDown';

function ProfileMessages(props) {
    const classes = useStyles()
    return (
        <div>
            <List className={classes.rootMessages}>
                <Avatar src={props.img} alt={props.id}/>
                <ListItem >
                    <Typography>
                        {props.message}
                    </Typography>
                </ListItem>
                <Button className={classes.buttons} size="small" color="primary" variant="contained">
                        <span>
                            <ThumbUpAltIcon fontSize="small"/>
                            <strong> {props.likes}</strong>
                        </span>
                </Button>
                <Button size="small" color="secondary" variant="contained">
                        <span>
                            <ThumbDownIcon fontSize="small"/>
                        </span>
                </Button>
            </List>
        </div>
    );
}

export default ProfileMessages;